@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}

nav{
  background-color: rgb(247, 138, 232);
  padding: 0rem;
  padding-bottom: 0;
  margin: 0;
  width: 100%;
  
}

.nav_title{
  color: rgb(5, 7, 151);
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 2rem;
  font-style: oblique;
  font-weight: 1000;
}

.nav_footer{
  color: rgb(5, 7, 151);
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 2rem;
  font-style: oblique;
  font-weight: 1000;
}

li{
  list-style: none;
}

.ul_container{
  margin-top: 1px;
  margin-bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.li_home{
 font-weight: 900;
  margin-top: 1rem;
  color: rgb(1, 1, 37);
  padding-bottom:20px ;
  text-indent: 9px;
}

.bottom_line:hover, .li_home:hover {
  color: #f1f1f1;
  border-botto: 4px solid rgb(5, 7, 151);
  text-decoration: none;
}


.form-bg{
background: linear-gradient(180deg, rgb(253, 216, 253) 75%,  white 20%);
}
.nav_icon {
  display: none;
}
.home {
  background-image: url(/static/media/fog.01bc9948.jpg);
  min-height: 100vh;
  background-attachment: fixed;
  background-size: cover;
}

.home-content {
  border: 3px solid black;
  width: 50rem;
  height: 300px;
  text-align: center;
  margin-top: 10rem;
  border-radius: 40px;
  background: rgba(235, 233, 250, 0.603);
}

.btn-box {
  position: relativ;
  margin-top: 7rem;
}

.btn1 {
  padding: 10px;
  width: 12pc;
  border-radius: 50px;
  border: 3px solid black;
}
.btn2 {
  padding: 10px;
  width: 12pc;
  border-radius: 50px;
  border: 3px solid black;
}

.btn1:hover {
  transform: translateY(-9px);
  background-color: rgba(46, 231, 71, 0.61);
  box-shadow: 2px 10px rgb(41, 42, 43);
}
.btn2:hover {
  transform: translateY(-9px);
  background-color: rgba(231, 46, 191, 0.61);
  box-shadow: 2px 10px rgb(41, 42, 43);
}
.card-f {
  box-shadow:2px 18px 15px 10px rgba(94, 91, 91, 0.37);
  transform: translateY(-20px);
  background:  rgb(229, 244, 253);
  border-radius: 10px;
  width: 500px;
  padding-left: 30px;
  padding-right: 30px;
}

.btn-log{
  width: 3cm;
  margin-left: 4cm;
  padding: 5px;
  background-color: rgba(91, 247, 85, 0.671);
  justify-content: center;
  display: flex;
  align-items: center;
  border-radius: 50px;
  font-weight: 900;
}

.btn-log:hover{
  background-color: rgb(3, 150, 10);
}

.footer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgba(90, 90, 138, 0.39);
  padding: 1rem;
  padding-bottom: 0;
  margin: 0;
}

.social-icon-link{
  padding-right: 1rem;
  font-size: 2rem;
  color: rgb(8, 158, 3);
}

.grid-container{
  display: flex;
  grid-gap: 10px;
  background-color: #f1f1f1;
  padding: 10px;
  width: 100%;
}

.item1 {
  width: 20%;
}

.item2 {
  width: 60%;
}

.item3 {
  width: 20%;
}

.card-container{
  display: grid;
  grid-gap: 10px;
}

.card1{
  background: crimson;
  grid-row: 1/ span 2;
  grid-column: 1/ span 2;
  
}
.card2{
  background: rgb(33, 20, 220);
  grid-row: 1/span 2;
  grid-column: 3;
}
.card3{
  background: rgb(66, 1, 1);
  grid-row: 2;
  grid-column: 2;
}
.card4{
  background: rgb(20, 220, 30);
  grid-row: 2;
  grid-column: 3;
}
.card5{
  background: rgb(187, 138, 4);
  grid-row: 3;
  grid-column: 1;
}
.card6{
  background: rgb(4, 165, 170);
  grid-row: 3;
  grid-column: 2/span 2;
}

@media screen and (max-width: 960px) {
  .nav_icon{
    position: absolute;
   display: block;
   top: 0;
   right: 0;
   cursor: pointer;
   transform: translate(-100%, 60%);
   font-size: 1.8rem;
  color: rgb(5, 7, 151);

  }

  .nav-menu {
    position: absolute;
    top: 60px;
    left: -100%;
    opacity: 1;
    align-items: center;
  justify-content: center;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: rgba(90, 90, 138, 0.952);
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }
  
.ul_container{
  margin-top: 4px;
  margin-bottom: 0;
  display: grid;
}

.li_home{
 font-weight: 900;
  margin-top: 1rem;
  color: rgb(5, 7, 151);
  padding:5px 100px ;
  text-align: left;
}

.bottom_line:hover, .li_home:hover {
  border-bottom: 4px solid rgb(14, 2, 126);
  text-decoration: none;
  background: rgba(90, 90, 138, 0.685);
color: white;
}

.nav_title {
  position: relative;
  margin-left: 100px;
  margin-right: 0%;
}
.footer {
  align-items: center;
  justify-content: center;
  display: block;
}

.nav_footer{
  color: rgb(5, 7, 151);
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 2rem;
  font-style: oblique;
  font-weight: 1000;
}
}


